<template>
  <div>
    <h2>Editar</h2>
    <form @submit.prevent="actualizarSocio">
            <div>
                <input
                    type='number'
                    name='cedula'
                    class='form-control'
                    placeholder='Cedula'
                    autoComplete='off'
                    v-model="editSocio.cedula"
                />
                <input
                    type='text'
                    name='nombre'
                    class='form-control mt-3'
                    placeholder='Nombre'
                    autoComplete='off'
                    v-model="editSocio.nombre"
                />
                <input
                    type='text'
                    name='apellidos'
                    class='form-control mt-3'
                    placeholder='Apellidos'
                    autoComplete='off'
                    v-model="editSocio.apellidos"

                />
                 <input
                    type='number'
                    name='celular'
                    class='form-control mt-3'
                    placeholder='Celular'
                    autoComplete='off'
                    v-model="editSocio.celular"

                />
                <input
                    type='text'
                    name='placa'
                    class='form-control mt-3'
                    placeholder='Placa'
                    autoComplete='off'
                    v-model="editSocio.placa"
       
                />

                <button 
                class='btn btn-primary form-control  mt-2'
                type='submit'  
                @click="actualizarSocio"
                >
                Actualizar

                </button>
            </div>
        </form>
    <button class="btn btn-success mt-5" @click="setEdit" >Salir</button>
  </div>
</template>

<script>
    import Swal from 'sweetalert2'
import { mapActions, mapMutations, mapState } from 'vuex'

export default {
    name: 'EditSocio',
    data(){
        return{
            editSocio:{},
            
        }
    },
    mounted(){
        
        this.editSocio = this.socio
    },
    computed: {
        ...mapState('socio',['socio']),
    },
    methods:{
            ...mapMutations('socio',['setEdit', 'closeEdit']),
            ...mapActions('socio',['updateSocio'])
        ,
        async actualizarSocio(){
                       
            await this.updateSocio(this.editSocio)
            this.setEdit()
            this.closeEdit()
        }
    }
}
</script>

